import React, { useState ,useEffect} from 'react'
import  NavbarItem  from './NavIbarItem'
import { NavLink } from 'react-router-dom'
import closeSymbol from './../../assest/svg/close.svg'
import menuSymbol from './../../assest/svg/menu.svg'
import { logout } from "./../../actions/auth";
import { useDispatch, useSelector } from "react-redux"
import useScroll from './../../hooks/useScroll'
import history from '../../helpers/history'
import './navBar.css'

const NavBar = () => {

  const { user: currentUser } = useSelector((state) => state.auth)
  const dispatch = useDispatch()
  const scroll = useScroll()

  const [click, setClick] = useState(false)
  const [navBar, setNavBar] = useState(false)
  
  const handleClick = () => setClick(!click)
  const closeMenu = () => setClick(false)
  const addLink = (link) => {
    history.push(link)
  }
  const navItems = [
    {title:"",link:"/"},
    {title:"",link:"/manasa"},
    {title:"",link:"/roiaa"},
    {title:"  ",link:"/contactus"},
  ]
  const changNavBar = () => {
    if (scroll >= 500) {
      setNavBar(true)
    } else {
      setNavBar(false)
    }
  }
  useEffect(() => {
     changNavBar()
  })

  
  const handelLogout = () => {
    dispatch(logout())
  }

  

  return (
    <>
      <div className='header'>
        <div className='slider-con'>
        <img src='assets/images/logo.png' alt='logo' className='logo' />
          
        </div>
        <div className='banner'>
          <div className={navBar ?'title active':'title'}>Al Kwther Private School</div>
          <div className='navbar'>
            <div className='logo-con'>
              <img src='assets/images/logo-b.png' alt='logo'
                className={ navBar ? 'logo-b active':'logo-b'} />
            </div>
            <ul className={ click ? 'nav-con' : navBar ? 'nav-con active close' : 'nav-con close' }>
             
              <div className='login-con'>

              { currentUser ? (
                  <li onClick={ () => {
                    handelLogout()
                    handleClick()
                  } }>
                 <NavLink to='/'>
                     
                    {currentUser.username}
                 </NavLink>
                 </li>
              ) : (
                <li>
                <NavLink to='/login'>
                   
                </NavLink>
                </li>
              ) }
                
               
              </div>
              <div className='links-con'>
                <NavbarItem
                  navItems={ navItems }
                  closeMenu={ closeMenu }
                  addLink={addLink}
                />
              </div>
            </ul>
          </div>
          <hr className='hr' />
          <div className='mobile-menu' onClick={handleClick}>
            {click ? (
              <img alt='' src={closeSymbol} />
            ) : (
              <img alt='' src={menuSymbol} />
            )}
          </div>
        </div>
      </div>
    </>
  )
}


export default NavBar
